@import './utils/Variables.scss';

.nota {
	font-size: 12px;
	font-family: Karla;
	background-color: $colorNota;
	width: 100%;
	margin: 5px 2.5vh 0.7vh 2.5vh;
	padding: 7px 12px;
	color: #ffffff;
}

.boton {
	font-size: 20px;
	font-family: Karla;
	width: 100%;
	height: 60px;
	background-color: $colorBase;
	margin: 0.7vh 2.5vh;
	padding: 10px 10px;
	color: white;
}

.icon {
	width: 40px;
	margin-right: 10px;
}

.sup {
	color: $colorSupFont;
	font-weight: bold;
	font-family: Karla;
	margin: 0px 5px;
	font-size: 10px;
	background-color: $colorSupBg;
	width: 100%;
	border-radius: 3px;
	padding: 1px 2px;
}